import React, { Component } from "react";
import "./styles.css";
import Select from "react-select";

// ÏïºÏòπÏù¥ ÏÉÅÌÉú ÏòµÏÖòÎì§
const options = [
  { value: "3.0", label: "ÏûÑÏã†Î¨ò or 4Í∞úÏõî ÎØ∏Îßå ÏïÑÍ∏∞ÏïºÏòπÏù¥" },
  { value: "2.5", label: "4~6Í∞úÏõîÏùò ÏïÑÍ∏∞ÏïºÏòπÏù¥" },
  { value: "2.0", label: "7~12Í∞úÏõîÏùò ÏïÑÍ∏∞ÏïºÏòπÏù¥" },
  { value: "1.6", label: "Ïö¥ÎèôÎüâÏù¥ ÎßéÍ≥† ÌôúÎ∞úÌïú Îã§ ÌÅ∞ ÏïºÏòπÏù¥" },
  { value: "1.4", label: "Ï§ëÏÑ±Ìôî ÌïòÏßÄ ÏïäÏùÄ Îã§ ÌÅ∞ ÏïºÏòπÏù¥" },
  { value: "1.2", label: "Ï§ëÏÑ±Ìôî Ìïú Îã§ ÌÅ∞ Í≥†ÏñëÏù¥" },
  { value: "1.0", label: "Ï†ÅÏ†ï Ï≤¥Ï§ëÏùò ÏïºÏòπÏù¥" },
  { value: "0.8", label: "Ï≤¥Ï§ë Í∞êÎüâÏù¥ ÌïÑÏöîÌïú ÏïºÏòπÏù¥" }
];

class CatInfoInput extends Component {
  state = {
    catName: "",
    catWeight: "",
    catAge: "",
    selectedOption: "",
    selectedBorder: "solid 1px #efefef",
    mealKcal: "",
    dayMeal: "",
    visible: "invisible"
  };

  // Î™∏Î¨¥Í≤å, ÎÇòÏù¥, ÏπºÎ°úÎ¶¨ Ïà´ÏûêÏôÄ Ïª¥ÎßàÎßå ÏûÖÎ†•. Í∑∏Î†áÏßÄ ÏïäÏúºÎ©¥ warnInputStyle
  handleChange = e => {
    const reg = /^[0-9]{0,3}\.?\d+?$/;
    const { name, value } = e.target;
    if (name === "catWeight" || name === "catAge" || name === "mealKcal") {
      if (reg.test(value)) {
        e.target.className = "inputStyle";
      } else {
        e.target.className = "warnInputStyle";
      }
    }
    // inputÏóê ÏûÖÎ†•Ìïú Í∞í ÏûÑÏãúÎ°ú stateÏóê Ï†ÄÏû•
    this.setState({ [name]: value });
  };

  //ÏïºÏòπÏù¥ ÏÉÅÌÉú ÏÑ†ÌÉù Ïãú setState. ÏïºÏòπÏù¥ ÏÉÅÌÉú ÏÑ†ÌÉùÌïòÏßÄ ÏïäÏùÑ Ïãú ÌÖåÎëêÎ¶¨ Î≥ÄÍ≤Ω
  handleChangeSelect = selectedOption => {
    this.setState({ selectedOption: selectedOption.value });
    if (selectedOption === null) {
      this.setState({ selectedBorder: "solid 1px #ff822f" });
    } else {
      this.setState({ selectedBorder: "solid 1px #efefef" });
    }
  };

  // ÌôïÏù∏ÌïòÍ∏∞ Î≤ÑÌäº ÌÅ¥Î¶≠ ÌõÑ Ïù∏Ìíã ÏûàÏúºÎ©¥ ÏÖãÏä§ÌÖåÏù¥Ìä∏, ÏóÜÏúºÎ©¥ Í≤ΩÍ≥†Ï∞Ω ÌõÑ ÌÖåÎëêÎ¶¨ Î≥ÄÍ≤Ω
  handleClick = e => {
    //Ïù¥Î≤§Ìä∏ Î∞úÏÉù Ïãú ÌéòÏù¥ÏßÄ Îã§Ïãú Î∂àÎü¨Ïò§ÏßÄ ÏïäÍ≤å ÏûëÏóÖ Î∞©ÏßÄ
    e.preventDefault();

    const { catName, catWeight, catAge, mealKcal, selectedOption } = this.state;
    if (
      // Ïù∏ÌíãÏùò ÏûÖÎ†•Í∏∏Ïù¥ ÌôïÏù∏
      catName.length > 0 &&
      catWeight.length > 0 &&
      typeof catWeight === typeof "0" &&
      catAge.length > 0 &&
      selectedOption !== null &&
      mealKcal.length > 0
    ) {
      // ÏûÖÎ†•Í∏∏Ïù¥Í∞Ä 0 Ïù¥ÏÉÅÏùºÎïå Î∂ÄÎ™®ÏóêÍ≤åÎ°ú stateÍ∞í Ï†ÑÎã¨
      this.props.onCreate(this.state);
    } else {
      // ÏûÖÎ†• Ïπ∏ Í∏∏Ïù¥ 0 Ïù¥ÏÉÅÏù¥ ÏïÑÎãêÎïå
      alert("ÎπàÏπ∏ÏùÑ Î™®Îëê ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî");
      if (catName.length === 0) {
        e.target.catName.className = "warnInputStyle";
      }
      if (catWeight.length === 0) {
        e.target.catWeight.className = "warnInputStyle";
      }
      if (catAge.length === 0) {
        e.target.catAge.className = "warnInputStyle";
      }
      if (selectedOption === null) {
        this.setState({ selectedBorder: "solid 1px #ff822f" });
      }
      if (mealKcal.length === 0) {
        e.target.mealKcal.className = "warnInputStyle";
      }
    }
  };

  render() {
    const { catName, catWeight, catAge, mealKcal } = this.state;
    //ÏïºÏòπÏù¥ ÏÉÅÌÉú Ïä§ÌÉÄÏùº Ïª§Ïä§ÌÖÄ
    const customStyles = {
      container: styles => ({
        ...styles,
        width: "220px"
      }),
      valueContainer: styles => ({
        ...styles,
        height: "100%",
        paddingLeft: "14px"
      }),
      control: styles => ({
        ...styles,
        height: "41px",
        borderRadius: "10px",
        border: this.state.selectedBorder
      }),
      placeholder: styles => ({
        ...styles,
        fontSize: "14px",
        letterSpacing: "-0.5px",
        color: "#d4d4d4",
        paddingRight: "14px"
      }),
      option: styles => ({
        ...styles,
        fontSize: "14px",
        letterSpacing: "-0.5px",
        color: "#626262"
      }),
      singleValue: styles => ({
        ...styles,
        fontSize: "14px",
        letterSpacing: "-0.5px",
        color: "#626262"
      }),
      indicatorSeparator: styles => ({
        ...styles,
        backgroundColor: "white"
      }),
      dropdownIndicator: styles => ({
        ...styles,
        color: "#c3c3c3"
      })
    };
    return (
      <>
        <form onSubmit={this.handleClick}>
          <div className="catInfo">
            <div className="catName row">
              <label>ÏïºÏòπÏù¥ Ïù¥Î¶Ñ</label>
              <input
                name="catName"
                placeholder="Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
                onChange={this.handleChange}
                value={catName}
                className={"inputStyle"}
              />
            </div>

            <div className="weightAndAge row">
              <div className="catWeight alignRight">
                <label>Î™∏Î¨¥Í≤å</label>
                <input
                  name="catWeight"
                  placeholder="kg"
                  onChange={this.handleChange}
                  value={catWeight}
                  className={"inputStyle"}
                />
              </div>
              <div className="catAge alignRight">
                <label>ÎÇòÏù¥</label>
                <input
                  name="catAge"
                  placeholder="ÏÇ¥"
                  onChange={this.handleChange}
                  value={catAge}
                  className={"inputStyle"}
                />
              </div>
            </div>
            <div className="catStatus row">
              <label>ÏïºÏòπÏù¥ ÏÉÅÌÉú</label>
              <div className="selectBox">
                <Select
                  onChange={this.handleChangeSelect}
                  options={options}
                  styles={customStyles}
                  isSearchable={false}
                />
              </div>
            </div>
            <div className="mealKcal row">
              <label>ÏÇ¨Î£å ÏπºÎ°úÎ¶¨</label>
              <div>
                <span>1kg Îãπ</span>
                <input
                  name="mealKcal"
                  placeholder="kcal"
                  onChange={this.handleChange}
                  value={mealKcal}
                  className={"inputStyle"}
                />
              </div>
            </div>
            <div className="submitButton">
              <button type="submit">ÌôïÏù∏ÌïòÍ∏∞</button>
            </div>
          </div>
        </form>
      </>
    );
  }
}

export default CatInfoInput;
